"use client"

import { useState } from 'react'
import { Button } from '@/components/ui/button'
import { Input } from '@/components/ui/input'
import { Label } from '@/components/ui/label'
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card'
import { Separator } from '@/components/ui/separator'
import { Mascot } from '@/components/indiko/mascot'
import { MobileNavigation } from '@/components/indiko/navigation'
import Link from 'next/link'

export default function IndicacaoPage() {
  const [formData, setFormData] = useState({
    nomeAmigo: '',
    whatsappAmigo: ''
  })
  const [isSubmitting, setIsSubmitting] = useState(false)
  const [linkCompartilhamento] = useState('https://indiko.app/ref/joao123')

  const handleInputChange = (field: string, value: string) => {
    setFormData(prev => ({ ...prev, [field]: value }))
  }

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    setIsSubmitting(true)
    
    // Simular envio
    setTimeout(() => {
      setIsSubmitting(false)
      // Redirecionar para confirmação
      window.location.href = '/confirmacao'
    }, 2000)
  }

  const handleCopyLink = () => {
    navigator.clipboard.writeText(linkCompartilhamento)
    // Aqui poderia mostrar um toast de sucesso
    alert('Link copiado!')
  }

  const handleWhatsAppShare = () => {
    const message = `Oi! Conhece o Indiko? É uma plataforma incrível onde você pode ganhar recompensas! Use meu link: ${linkCompartilhamento}`
    const whatsappUrl = `https://wa.me/?text=${encodeURIComponent(message)}`
    window.open(whatsappUrl, '_blank')
  }

  const handleInstagramShare = () => {
    // Para Instagram, normalmente seria através de stories ou posts
    const message = `Confira o Indiko! ${linkCompartilhamento}`
    navigator.clipboard.writeText(message)
    alert('Texto copiado! Cole no seu Instagram.')
  }

  return (
    <div className="min-h-screen bg-gradient-to-br from-green-50 to-white pb-20">
      {/* Header */}
      <div className="bg-white shadow-sm">
        <div className="px-6 py-4">
          <div className="flex items-center justify-between">
            <div className="flex items-center space-x-3">
              <Link href="/dashboard">
                <Button variant="ghost" size="sm" className="rounded-full">
                  ←
                </Button>
              </Link>
              <div>
                <h1 className="font-poppins font-bold text-xl text-gray-900">
                  Indicar Amigo
                </h1>
                <p className="text-sm text-gray-600">
                  Ganhe pontos a cada indicação!
                </p>
              </div>
            </div>
            
            <Mascot size="sm" animated={true} />
          </div>
        </div>
      </div>

      {/* Main Content */}
      <div className="px-6 py-6 space-y-6">
        {/* Incentivo */}
        <Card className="bg-gradient-to-r from-green-600 to-green-700 text-white border-0 shadow-lg">
          <CardContent className="p-6 text-center">
            <Mascot size="md" celebrating={true} className="mx-auto mb-4" />
            <h2 className="font-poppins font-bold text-lg mb-2">
              Indique e Ganhe!
            </h2>
            <p className="text-green-100">
              Cada amigo que se cadastrar te dá <span className="font-bold">100 pontos</span>!
            </p>
          </CardContent>
        </Card>

        {/* Formulário de Indicação */}
        <Card className="shadow-md">
          <CardHeader>
            <CardTitle className="font-poppins font-bold text-lg">
              Dados do seu Amigo
            </CardTitle>
          </CardHeader>
          <CardContent>
            <form onSubmit={handleSubmit} className="space-y-4">
              <div className="space-y-2">
                <Label htmlFor="nomeAmigo" className="font-semibold">
                  Nome do Amigo
                </Label>
                <Input
                  id="nomeAmigo"
                  type="text"
                  placeholder="Nome completo do seu amigo"
                  value={formData.nomeAmigo}
                  onChange={(e) => handleInputChange('nomeAmigo', e.target.value)}
                  className="h-12 rounded-xl border-2 focus:border-green-500"
                  required
                />
              </div>

              <div className="space-y-2">
                <Label htmlFor="whatsappAmigo" className="font-semibold">
                  WhatsApp do Amigo
                </Label>
                <Input
                  id="whatsappAmigo"
                  type="tel"
                  placeholder="(11) 99999-9999"
                  value={formData.whatsappAmigo}
                  onChange={(e) => handleInputChange('whatsappAmigo', e.target.value)}
                  className="h-12 rounded-xl border-2 focus:border-green-500"
                  required
                />
              </div>

              <Button 
                type="submit"
                disabled={isSubmitting}
                className="w-full h-12 mt-6 bg-green-600 hover:bg-green-700 text-white font-semibold rounded-xl shadow-lg hover:shadow-xl transition-all duration-300 disabled:opacity-50"
              >
                {isSubmitting ? (
                  <div className="flex items-center space-x-2">
                    <div className="w-4 h-4 border-2 border-white border-t-transparent rounded-full animate-spin"></div>
                    <span>Enviando...</span>
                  </div>
                ) : (
                  'Enviar Convite'
                )}
              </Button>
            </form>
          </CardContent>
        </Card>

        {/* Separador */}
        <div className="flex items-center space-x-4">
          <Separator className="flex-1" />
          <span className="text-gray-500 font-medium">ou</span>
          <Separator className="flex-1" />
        </div>

        {/* Compartilhamento */}
        <Card className="shadow-md">
          <CardHeader>
            <CardTitle className="font-poppins font-bold text-lg">
              Compartilhe seu Link
            </CardTitle>
          </CardHeader>
          <CardContent className="space-y-4">
            <div className="p-4 bg-gray-50 rounded-xl">
              <Label className="font-semibold text-sm text-gray-700 mb-2 block">
                Seu link de indicação:
              </Label>
              <div className="flex items-center space-x-2">
                <Input
                  value={linkCompartilhamento}
                  readOnly
                  className="flex-1 bg-white border-2"
                />
                <Button
                  onClick={handleCopyLink}
                  variant="outline"
                  className="px-4 py-2 border-2"
                >
                  Copiar
                </Button>
              </div>
            </div>

            <div className="space-y-3">
              <Button
                onClick={handleWhatsAppShare}
                className="w-full h-12 bg-green-500 hover:bg-green-600 text-white font-semibold rounded-xl flex items-center justify-center space-x-3"
              >
                <div className="w-6 h-6 bg-white rounded-full flex items-center justify-center">
                  <div className="w-4 h-4 bg-green-500 rounded-full"></div>
                </div>
                <span>Compartilhar no WhatsApp</span>
              </Button>

              <Button
                onClick={handleInstagramShare}
                variant="outline"
                className="w-full h-12 border-2 border-pink-500 text-pink-500 hover:bg-pink-50 font-semibold rounded-xl flex items-center justify-center space-x-3"
              >
                <div className="w-6 h-6 bg-gradient-to-r from-purple-500 to-pink-500 rounded-full flex items-center justify-center">
                  <div className="w-4 h-4 bg-white rounded-full"></div>
                </div>
                <span>Compartilhar no Instagram</span>
              </Button>

              <Button
                onClick={handleCopyLink}
                variant="outline"
                className="w-full h-12 border-2 font-semibold rounded-xl flex items-center justify-center space-x-3"
              >
                <div className="w-6 h-6 bg-gray-500 rounded-full flex items-center justify-center">
                  <div className="w-4 h-4 bg-white rounded-full"></div>
                </div>
                <span>Copiar Link</span>
              </Button>
            </div>
          </CardContent>
        </Card>

        {/* Dicas */}
        <Card className="bg-blue-50 border-blue-200 shadow-md">
          <CardContent className="p-4">
            <h3 className="font-poppins font-bold text-blue-900 mb-2">
              💡 Dicas para mais indicações:
            </h3>
            <ul className="text-sm text-blue-800 space-y-1">
              <li>• Explique os benefícios do Indiko para seu amigo</li>
              <li>• Mencione as recompensas que ele pode ganhar</li>
              <li>• Ajude-o no processo de cadastro se necessário</li>
            </ul>
          </CardContent>
        </Card>
      </div>

      <MobileNavigation />
    </div>
  )
}
