"use client"

import { useState, useEffect } from 'react'

interface MascotProps {
  size?: 'sm' | 'md' | 'lg' | 'xl'
  animated?: boolean
  celebrating?: boolean
  className?: string
}

export function Mascot({ 
  size = 'md', 
  animated = false, 
  celebrating = false,
  className = '' 
}: MascotProps) {
  const [isAnimating, setIsAnimating] = useState(false)

  const sizeClasses = {
    sm: 'w-16 h-16',
    md: 'w-24 h-24',
    lg: 'w-32 h-32',
    xl: 'w-48 h-48'
  }

  useEffect(() => {
    if (animated) {
      const interval = setInterval(() => {
        setIsAnimating(true)
        setTimeout(() => setIsAnimating(false), 1000)
      }, 3000)
      return () => clearInterval(interval)
    }
  }, [animated])

  return (
    <div className={`${sizeClasses[size]} ${className} relative flex items-center justify-center`}>
      <div 
        className={`
          relative w-full h-full transition-transform duration-500
          ${isAnimating || celebrating ? 'animate-bounce' : ''}
          ${celebrating ? 'scale-110' : ''}
        `}
      >
        {/* Corpo do Tucano */}
        <div className="absolute inset-0 bg-gradient-to-br from-blue-500 to-blue-600 rounded-full shadow-lg">
          {/* Bico multicolorido */}
          <div className="absolute -right-2 top-1/3 w-8 h-4 bg-gradient-to-r from-yellow-400 via-green-500 to-blue-500 rounded-r-full transform rotate-12"></div>
          
          {/* Olhos */}
          <div className="absolute top-1/4 left-1/3 w-3 h-3 bg-white rounded-full">
            <div className="absolute top-1 left-1 w-1.5 h-1.5 bg-black rounded-full"></div>
          </div>
          
          {/* Asa */}
          <div className="absolute right-2 top-1/2 w-6 h-8 bg-blue-700 rounded-full transform -rotate-12 opacity-80"></div>
          
          {/* Barriga */}
          <div className="absolute bottom-2 left-1/2 transform -translate-x-1/2 w-8 h-6 bg-yellow-300 rounded-full opacity-90"></div>
        </div>

        {/* Efeito de celebração */}
        {celebrating && (
          <>
            <div className="absolute -top-2 -left-2 w-2 h-2 bg-yellow-400 rounded-full animate-ping"></div>
            <div className="absolute -top-1 -right-3 w-1.5 h-1.5 bg-green-500 rounded-full animate-ping delay-100"></div>
            <div className="absolute -bottom-2 -right-1 w-2 h-2 bg-blue-500 rounded-full animate-ping delay-200"></div>
          </>
        )}
      </div>
    </div>
  )
}
