"use client"

import Link from 'next/link'
import { usePathname } from 'next/navigation'

interface NavItem {
  href: string
  label: string
  icon: React.ReactNode
}

export function MobileNavigation() {
  const pathname = usePathname()

  const navItems: NavItem[] = [
    {
      href: '/dashboard',
      label: 'Início',
      icon: (
        <div className="w-6 h-6 flex items-center justify-center">
          <div className="w-4 h-4 bg-current rounded"></div>
        </div>
      )
    },
    {
      href: '/indicacao',
      label: 'Indicar',
      icon: (
        <div className="w-6 h-6 flex items-center justify-center">
          <div className="w-4 h-4 border-2 border-current rounded-full flex items-center justify-center">
            <div className="w-1 h-1 bg-current rounded-full"></div>
          </div>
        </div>
      )
    },
    {
      href: '/historico',
      label: 'Histórico',
      icon: (
        <div className="w-6 h-6 flex items-center justify-center">
          <div className="space-y-1">
            <div className="w-4 h-0.5 bg-current rounded"></div>
            <div className="w-3 h-0.5 bg-current rounded"></div>
            <div className="w-4 h-0.5 bg-current rounded"></div>
          </div>
        </div>
      )
    },
    {
      href: '/recompensas',
      label: 'Prêmios',
      icon: (
        <div className="w-6 h-6 flex items-center justify-center">
          <div className="w-4 h-4 bg-current rounded-full relative">
            <div className="absolute -top-1 -right-1 w-2 h-2 bg-yellow-400 rounded-full"></div>
          </div>
        </div>
      )
    },
    {
      href: '/perfil',
      label: 'Perfil',
      icon: (
        <div className="w-6 h-6 flex items-center justify-center">
          <div className="w-4 h-4 bg-current rounded-full"></div>
        </div>
      )
    }
  ]

  return (
    <nav className="fixed bottom-0 left-0 right-0 bg-white border-t border-gray-200 px-4 py-2 z-50">
      <div className="flex items-center justify-around max-w-md mx-auto">
        {navItems.map((item) => {
          const isActive = pathname === item.href
          return (
            <Link
              key={item.href}
              href={item.href}
              className={`
                flex flex-col items-center justify-center p-2 rounded-lg transition-all duration-200
                ${isActive 
                  ? 'text-blue-600 bg-blue-50' 
                  : 'text-gray-500 hover:text-gray-700 hover:bg-gray-50'
                }
              `}
            >
              <div className="mb-1">
                {item.icon}
              </div>
              <span className="text-xs font-medium">
                {item.label}
              </span>
            </Link>
          )
        })}
      </div>
    </nav>
  )
}
